.Game {
  text-align: center;
  width: 600px;
  height: 400px;
  border: solid 1px white;
  position: relative;
}

.Home
{
  height: 100%;
  width: 100%;  
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
}

.Target
{
  width: 100px;
  margin-right: 40px;
}

.Target p
{
  color: white;
  font: 1.2rem "Fira Sans", sans-serif;
}

.Score
{
  width: 150px;
  margin-left: 20px;
}

.Score p, #gameover, #gameoverScore
{
  font: 1.2rem "Fira Sans", sans-serif;
}

#gameover
{
  color: red;
}

.Score #score, #gameoverScore
{
  color: white;
}

.Score #timer
{
  color: yellow;
}

.imgToClick {
  height: 30px;
  width: 30px;
  position: absolute;
}

html {
  background-color: black;
}